.table {
  width: 100%;
  border-collapse: collapse;
}

.thead {
  border-bottom: 1px solid #eeeff1;
}

.th {
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.15px;
  color: #4e5c69;
  padding: 12px 20px;
  text-align: left;
}

.td {
  font-size: 12px;
  line-height: 16px;
  color: #2c343c;
  padding: 12px 20px;
  text-align: left;
}

.td span {
  background-color: #eeeff1;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  padding: 6px 8px 4px;
  font-size: 10px;
  line-height: 12px;
  letter-spacing: 0.15px;
  color: #3f4954;
}

@media (max-width: 800px) {
  .thead {
    left: -9999px;
    position: absolute;
    visibility: hidden;
  }

  .tr {
    border-bottom: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 28px;
  }

  .td {
    border: 1px solid #eeeff1;
    margin: 0 -1px -1px 0;
    padding: 32px 8px 8px;
    position: relative;
  }

  .td span {
    display: block;
  }

  .tdNumber {
    display: none;
  }

  .tdRowName {
    grid-column-start: 1;
    grid-column-end: 3;
    border: none;
    padding-top: 8px;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.15px;
  }

  .tdRowName span {
    display: none;
  }
}
